body {
    margin: 1vh 1vw;
    background-color: #efe;
}

ul,
fieldset,
legend {
    border: 1px solid;
    background-color: #fff;
}

ul {
    padding: 0;
    font-family: monospace;
}

li,
legend {
    list-style-type: none;
    padding: 0.2em 0.5em;
    background-color: #cfc;
}

li:nth-of-type(even) {
    background-color: inherit;
}